.home {
	width: 100%;
	height: 80vh;
	display: flex;
	justify-content: center;
	align-items: left;
	flex-direction: column;
	background-position: center;
	background-repeat: no-repeat;
	background-size: cover;
	font-family: "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS", sans-serif;
}

.headerContainer {
	width: auto;
	margin-left: 50px;
}

.headerContainer h1 {
	justify-content: center;
	align-items: center;
	font-size: 5rem;
	font-weight: bold;
	color: black;
}

.headerContainer p {
	padding: 0;
	font-size: 40px;
	font-weight: lighter;
	color: black;
}

.headerContainer button {
	color: black;
	border: none;
	border-radius: 5px;
	cursor: pointer;
	justify-content: center;
	background-color: #ea3b00;
	height: 50px;
	width: 180px;
	color: white;
	font-weight: bold;
	text-decoration: none;
	font-size: 20px;
}

.headerContainer button:hover {
	background-color: #f75200;
	cursor: pointer;
	transition: 0.3s ease-in;
}

@media only screen and (max-width: 650px) {
	.home {
		justify-content: center;
		align-items: center;
	}

	.headerContainer {
		margin-left: 0px;
		border-radius: 10px;
		padding: 10px;
		width: 80%;
		display: flex;
		justify-content: center;
		align-items: center;
		flex-direction: column;
		background-color: #121619;
		color: white;
	}

	.headerContainer h1 {
		font-size: 40px;
		height: 30px;
		color: white;
	}

	.headerContainer p {
		font-size: 30px;
		color: white;
	}

	.headerContainer button {
		background-color: white;
		color: #ea3b00;
		margin-bottom: 30px;
	}

	.headerContainer button:hover {
		background-color: rgb(225, 225, 225);
		color: #f75200;
	}
}
